
























export default {
	data() {
		return {
			inputClearValue: "发送验证码",
			mobile:"",
			loading:false,
			is_sms:true,
			Loop:'',
			downtime:60,
			verifyCode:'',
			smsCode:'',
			toverifyCode:''
		}
	},
	onLoad(e) {
		if(e.toverifyCode){
			this.toverifyCode = e.toverifyCode;
		}else{
			uni.navigateTo({
				url: '/pages/user/info/info'
			})
		}
	},
	onShow() {
	},
	onShareAppMessage() {
		return {
			title: this.webshareTitle,
			path: '/pages/tabBar/index/index'
		}
	},
	methods: {
		editPhone: function (e) {
			const openid = this.$store.state.openid;
			const sessionKey = this.$store.state.sessionKey;
			const verifyCode = this.verifyCode;
			const smsCode = this.smsCode;
			const phone = this.mobile;
			const toverifyCode = this.toverifyCode;
			const that = this;
			try {
				if(this.loading){
					return;
				}
				this.loading = true;
				
				this.$http.post(this.websiteUrl + '/api/api/editPhone', {
					openid,
					sessionKey,
					verifyCode,
					smsCode,
					phone,
					toverifyCode
				}, (res) => {
					if (res.data.code == 1) {
						uni.showModal({
							content: res.data.msg,
							showCancel: false,
							success:function() {
								uni.navigateTo({
									url: '/pages/user/info/info'
								})
							}
						})
					}else{
						uni.showModal({
							content: res.data.msg,
							showCancel: false,
							success:function() {
								that.loading = false;
							}
						})
					}
				});
			} catch (e) {
				uni.showModal({
					content: '网络异常,请稍后重试...',
					showCancel: false,
					success:function() {
						that.loading = false;
					}
				});
			}	
			
		},
		sendSMS: function (e) {
			if(this.is_sms){
				this.is_sms = false;
				const openid = this.$store.state.openid;
				const sessionKey = this.$store.state.sessionKey;
				const phone = this.mobile;
				if(phone ==='' ){
					uni.showModal({
						content:'请填写手机号',
						showCancel:false
					})
					return false;
				}
				try {
					this.$http.post(this.websiteUrl + '/api/api/sendSMS', {
						openid,
						sessionKey,
						phone
					}, (res) => {
						if (res.data.code == 1) {
							this.downtime =60;
							this.dateDown();
							this.verifyCode = res.data.result;
						}else{
							uni.showModal({
								content: res.data.msg,
								showCancel: false
							})
							this.is_sms = true;
						}
					});
				} catch (e) {
					uni.showModal({
						content: '网络异常,请稍后重试...',
						showCancel: false
					})
					this.is_sms = true;
				}	
			}	
		},
		dateDown: function () {
			const that = this;
			this.inputClearValue='重新发送('+this.downtime+')';
			this.Loop = setInterval(function() {
				if(that.downtime == 0){
					that.inputClearValue='发送验证码';
					that.is_sms = true;
					clearInterval(that.Loop);
					return;
				}
				that.downtime--;
				that.inputClearValue='重新发送('+this.downtime+')';
			}.bind(this), 1000);
			if(this.is_sms){
				clearInterval(this.Loop);
			}
		},
		setNickname: function () {
			const openid = this.$store.state.openid;
			const sessionKey = this.$store.state.sessionKey;
			const nickname = this.inputClearValue;
			if(nickname==''){
				uni.showModal({
					content: '请填写昵称',
					showCancel: false
				});
				return;
			}
			try {
				if(this.loading){
					return;
				}
				this.loading = true;
				const that = this;
				this.$http.post(this.websiteUrl + '/api/api/setNickname', {
					openid,
					sessionKey,
					nickname
				}, (res) => {
					uni.showToast({
						title: res.data.msg,
						success:function() {
							that.loading = false;
						}
					})
					
				});
			} catch (e) {
				uni.showModal({
					content: '网络异常,请稍后重试...',
					showCancel: false
				});
				this.loading = false;
			}
		}
	}
}
